.overlayExp {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(0, 128, 0, 0.6) 0%, rgba(0,0,0,0.9) 100%); /* Cambiar el valor de opacidad a 0.9 para hacerlo más oscuro */
  z-index: 2;
}

.overlayExp2 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background:rgba(0,0,0,0.9); /* Cambiar el valor de opacidad a 0.9 para hacerlo más oscuro */
  z-index: 3;
}

.background-image {
  position: absolute; /* Cambiar de fixed a absolute */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover; /* Ajusta la imagen para que cubra todo el espacio */
  z-index: 1; /* Asegúrate de que esté detrás del contenedor y de la pantalla semitransparente */
}

.containerExp {
  position: relative;
  width: 100%;
  height: 100vh;
  color: #fff; /* Texto brillante para contraste */
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  background-color: transparent; 
  z-index: 4;
}

.scrollContainer {
  width: 100%;
  height: 80%;
  justify-content: center;
  align-items: center;
  max-width: 600px;
/* Fondo oscuro para la línea de tiempo */
}

.timeline {
   scrollbar-width: none; /* Firefox */
  height: 100%;
padding: 2%; /* Centra la línea de tiempo */

  overflow-y: auto;
}

.timeline::-webkit-scrollbar {
 display: none; /* Chrome, Safari, Edge */
}

.evento {
  width: calc(50% - 10px); /* Ajusta el ancho de las tarjetas */
  padding: 20px;
  margin-top: 60px;
  margin-bottom: 40px; /* Aumenta el espacio para la línea de conexión */
  border: 4px solid; /* Borde alrededor de toda la tarjeta */
  background-color: transparent; /* Fondo transparente para las tarjetas */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Efecto de elevación para las tarjetas */
  border-radius: 15px; /* Esquinas redondeadas */
}

/* Tarjetas alineadas a la izquierda */
.evento:nth-child(odd) {
  margin-left: 50%;
  transform: translateX(-50%) translateY(-50%); /* Ajusta la posición */
}

/* Tarjetas alineadas a la derecha */
.evento:nth-child(even) {
  margin-left: 40%;
  transform: translateY(-50%); /* Ajusta la posición */
}

.fecha {
  font-weight: bold;
  margin-bottom: 5px;
}

.descripcion {
  margin-top: 10px;
}

.linea-conexion {
  position: absolute;
  width: 2px; /* Ancho de la línea */
  left: 50%; /* Posición de inicio de la línea */
  bottom: 20px; /* Alineado con la parte inferior de la tarjeta */
  background-color: inherit; /* Toma el color de la tarjeta */
  z-index: -1; /* Coloca la línea detrás de las tarjetas */
}


.contentMotion {
  display: flex;
  width: 100%;
  height: 100%;
}

.txt_titulo_exp{
  color: rgb(6, 32, 9); /* Color del texto */
  font-weight: bold; 
  text-shadow: 
    -1px -1px 0 #c2fa94,  
     1px -1px 0 #c2fa94,
    -1px  1px 0 #c2fa94,
     1px  1px 0 #c2fa94; /* Agrega sombras para simular el borde blanco */;
     font-size: 40px;
}
.containerTittle{
  margin-bottom: 2%;
}

@media (max-width: 700px) {
  .txt_titulo_exp{
    font-size: 24px;
  }

  .fecha {
    font-weight: lighter;
    margin-bottom: 2px;
  }

  .descripcion {
    margin-top: 2px;
    font-weight: lighter;
    font-size: 14px;
  }

  .evento{
    padding: 15px;
    border: 2px, solid;
  }
}
