.pageP {
    position: relative;
    overflow: hidden;
    height: 100vh;
}

.animationContent{
  width: 100%;
  height: auto;
}

.video-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  
  .video-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
  }
  
  .video-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.9); /* Ajusta el nivel de opacidad según lo necesites */
    z-index: -1;
  }

  .subContentP{
    display: inline-flex;
    width: 100%;
    height: 90%;
  }

  .subContentP2{
    display: inline-flex;
    width: 100%;
    height: 90%;
    justify-content: center;
    align-items: center;
  }

  .contentPerfil {
    display: block;
    width: 100%;
    height: 100vh;
  }

.contenTxtPerfil{
    width: 50%;
    height: 100%;
}

.contenTxtPerfil2{
  width: 30%;
  flex-direction: column;
}

.contentTarjetas{
  width: 100%;
    height: 45vh;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    
}

.contentButtons{
  width: 100%;
  height: 10%;
  display: flex;
  flex-direction: column;
  align-items: end;
  justify-content: center;
}

.divButton{
  margin-right: 10%;
}

.sunbContentTxt{
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
}

.divContentTxt{
    display: block;
    width: 60%
}


.txtPerfil{
    /* Fuente Roboto */
font-family: "Roboto", sans-serif;

/* Tamaño de fuente */
font-size: 20px;

/* Color blanco */
color: #ffffff; /* Código hexadecimal */
}

.txtPerfilTit{
    /* Fuente Roboto */
font-family: "Roboto", sans-serif;

/* Tamaño de fuente */
font-size: 24px;
font-weight: bold;
/* Color blanco */
color: #ffffff; /* Código hexadecimal */
}

.contetTit{
    margin-bottom: 2%;
}


@media (max-width: 700px) {
    .subContentP{
        display: block;
      }

      .contenTxtPerfil{
        width: 100%;
        height: 50%;
    }

    .divContentTxt{
        display: block;
        width: 80%;
        height: 70%;
    }

    .contenTxtPerfil2{
      width: 40%;
      flex-direction: column;
      padding: 3%;
    }
}