.myCard {
  background-color: transparent;
  width: 290px;
  height: 264px;
  perspective: 1000px;
  z-index: 888;
}

.title {
  font-size: 1.5em;
  font-weight: 900;
  text-align: center;
  margin: 0;
}

.innerCard {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
  cursor: pointer;
}

.myCard:hover .innerCard {
  transform: rotateY(180deg);
}

.frontSide,
.backSide {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border: 1px solid rgba(255, 255, 255, 0.8);
  border-radius: 1rem;
  color: white;
  box-shadow: 0 0 0.3em rgba(255, 255, 255, 0.5);
  font-weight: 700;
}

.frontSide,
.frontSide::before {
  background: linear-gradient(
    43deg,
    rgb(65, 88, 208) 0%,
    rgb(200, 80, 192) 46%,
    rgb(255, 204, 112) 100%
  );
}

.backSide,
.backSide::before {
  background-image: linear-gradient(160deg, #0093e9 0%, #80d0c7 100%);
}

.backSide {
  transform: rotateY(180deg);
}

.frontSide::before,
.backSide::before {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  content: "";
  width: 110%;
  height: 110%;
  position: absolute;
  z-index: -1;
  border-radius: 1em;
  filter: blur(20px);
  animation: animate 5s linear infinite;
}

.contentInfC {
  width: 100%;
  height: 100%;
  flex-direction: column;
}

.contenItem {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 1%;
  margin-top: 10px;
}

.subContenItemTxt {
  width: 40%;
  justify-content: start;
  margin-left: 20px;
  display: flex;
}

.subContenItenStar {
  width: 60%;
  display: flex;
  justify-content: end;
  margin-right: 20px;
}

.txtItem {
  font-size: 18px;
  color: white;
}

@keyframes animate {
  0% {
    opacity: 0.3;
  }

  80% {
    opacity: 1;
  }

  100% {
    opacity: 0.3;
  }
}

@media (max-width: 700px) {
  .contenItem {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 1%;
    margin-top: 5px;
  }
  
  .subContenItemTxt {
    width: 100%;
    justify-content: center;
    display: flex;
    margin-left: 0;
  }
  
  .subContenItenStar {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-right: 0;
  }
  
  .txtItem {
    font-size: 18px;
    color: white;
  }
}
