/* Mas.css */

/* Estilos para el contenedor principal */
.containerMas {
    height: 100vh;
    width: 100%;
    background-color: rgb(41, 38, 38);
    overflow-y: auto; /* Habilita el desplazamiento vertical si el contenido excede la altura */
    position: relative; /* Establece una posición relativa para los elementos hijos posicionados absolutamente */
    scrollbar-width: none; /* Firefox */
  }
  
  /* Estilos para la barra de navegación */
  .navbar {
    position: fixed; /* Fija la barra de navegación en la parte superior de la ventana */
    top: 0;
    left: 0;
    height: 8%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.8); /* Fondo semi-transparente */
    color: white;
    display: flex;
    justify-content: right;
    align-items: center;
    transition: top 0.3s; /* Transición suave al cambiar la propiedad 'top' */
  }
  
  .nav-list {
    list-style-type: none; /* Elimina los estilos de la lista */
    margin: 0;
    margin-right: 50px;
    padding: 0;
    text-align: center; /* Centra los elementos de la lista */
  }
  
  .nav-item {
    display: inline-block; /* Muestra los elementos en línea */
    margin: 0 15px; /* Espacio entre los elementos de la lista */
  }
  
  .nav-item button {
    background: none;
    border: none;
    color: white;
    cursor: pointer;
    font-size: 16px;
  }
  
  .nav-item button:hover {
    color: #ccc; /* Cambia el color del texto al pasar el mouse por encima */
  }
  
  /* Estilos para las secciones */
  .section {
    height: 100vh; /* Establece la altura de cada sección al 100% de la altura de la ventana */
    padding: 50px 0; /* Espaciado interno */
    color: white;
    text-align: center;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  
  .section h2 {
    margin-bottom: 20px;
    font-size: 30px;
  }
  
  .txtImportante {
    margin: 0 auto;
    max-width: 80%;
    text-align: left;
    color: white;
    font-size: 20px;
  }

  .txtImportante3 {
    margin-left: 8%;
    margin-right: 10px;
    text-align: left;
    color: white;
    font-size: 20px;
  }

  .txtImportante2 {
    text-align: left;
    color: rgb(0, 174, 255);
    font-size: 20px;
    text-decoration: underline;
  }

  .containerEstudios{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90%;
    height: 80%;
    flex-direction: column;
  }

  .subContainerEstu{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 50px;
  }

  .contNameEst{
    width: 60%;
    height: 100%;
    display: flex;
    align-items: center;
  }

  .contFechEst{
    width: 25%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .contVerMas{
    width: 15%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .txtNameEst{
    color: white;
    font-size: 20px;
  }
  

  .divRedes{
    width: 80%;
    margin-top: 5%;
  }

  .divDescargar{
    margin-left: 50px;
    width: 100%;
    display: flex;
    justify-content:left;
    align-items: center;
    margin-top: 5%;
  }

  /* Media query para estilos responsivos */
  @media (max-width: 768px) {

    .navbar{
        height: 7%;
    }

    .nav-list {
      display: flex;
      flex-direction: row; /* Cambia la dirección de la lista a vertical en dispositivos móviles */
      align-items: center; /* Centra los elementos de la lista */
      margin-right: 20px;
    }
  
    .nav-item {
      margin: 15px 0; /* Espacio entre los elementos de la lista */
    }

    .txtNameEst{
      color: white;
      font-size: 13px;
      text-align: left;
    }

    .txtImportante{
      font-size: 16px;
    }

    .txtImportante2{
      font-size: 16px;
    }

    .txtImportante3{
      font-size: 16px;
    }

    .divDescargar{
      margin-left: 10px;
    }
  
  }
  
  


