.buttonVer {
    width: 10em;
    position: relative;
    height: 3.5em;
    border: 3px ridge #149CEA;
    outline: none;
    background-color: transparent;
    color: white;
    transition: 1s;
    border-radius: 0.3em;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
  }
  
  .buttonVer::after {
    content: "";
    position: absolute;
    top: -10px;
    left: 3%;
    width: 95%;
    height: 40%;
    background-color: rgb(80, 75, 75);
    transition: 0.5s;
    transform-origin: center;
  }
  
  .buttonVer::before {
    content: "";
    transform-origin: center;
    position: absolute;
    top: 80%;
    left: 3%;
    width: 95%;
    height: 40%;
    background-color: rgb(80, 75, 75);
    transition: 0.5s;
  }
  
  .buttonVer:hover::before, button:hover::after {
    transform: scale(0)
  }
  
  .buttonVer:hover {
    box-shadow: inset 0px 0px 25px #1479EA;
  }

  @media (max-width: 700px) {
    .buttonVer{
        height: 2.5em;
    }
  }