.containerProyects {
  height: 100vh;
  width: 100%;
  background-color: rgb(41, 38, 38);
  display: flex;
  flex-direction: column;
}

.barnerProyects {
  height: 25%;
  width: 100%;
}

.containerTarjetsPro {
  height: 70%;
  background-color: rgb(53, 50, 50);
  width: 80%;
  margin-top: 1%;
  margin-left: 10%;
  margin-right: 10%;
  box-shadow: 0 16px 8px rgba(0, 0, 0, 0.1); /* Aquí defines la sombra */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.contentTitleProyects {
  width: 100%;
  display: flex;
  height: 12%;
  justify-content: center;
  align-items: center;
}

.txt_title_proyects {
  font-size: 28px;
  color: white;
}

.contentSlider {
  width: 100%;
  height: 80%;
  display: flex;
  flex-direction: row;
}

.contentArrow {
  width: 10%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  align-items: center;
}

.contentInfoPro {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  border-radius: 18px; /* Ajusta el valor según el grado de redondeo que desees */
  box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.1); /* Ajusta el valor de sombra según sea necesario */
  background-color: rgb(80, 75, 75);
  flex-direction: column;
}

.mySwiper {
  width: 80%;
  height: 100%;
}

.sliderSw {
  width: 100%;
  height: 100%;
}

.backImgArrow {
  width: 40%;
  height: 20%;
}

.titleTarjetProyect {
  width: 100%;
  height: 15%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.txtTitleProyect {
  color: white;
  font-size: 30px;
}

.contentProyects {
  width: 100%;
  height: 80%;
  display: flex;
  flex-direction: row;
}

.contentImgProyect {
  width: 35%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contentResumeProyect {
  width: 65%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.imageProyect {
  width: 80%;
  height: 70%;
  border-radius: 15px;
}

.contentResum {
  width: 100%;
  height: 80%;
  display: flex;

  align-items: center;
}

.txtResumen {
  font-size: 20px;
  color: white;
}

.contentButonVer {
  width: 100%;
  height: 20%;
  display: flex;
  align-items: center;
  justify-content: end;
}

.subContenButonVer {
  margin-right: 5%;
}

.contenedorAlert {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: rgba(
    128,
    128,
    128,
    0.5
  ); /* Gris con 50% de transparencia */
}

.subContenedorAlert {
  background-color: rgb(230, 216, 216);
  box-shadow: 0 16px 8px rgba(0, 0, 0, 0.1); /* Aquí defines la sombra */
  border-radius: 10px;
  width: 60%;
  height: 70%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.contentTitleDes {
  width: 100%;
  height: 10%;
  display: flex;
  flex-direction: row;
}

.subContentTitleDes {
  width: 80%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.divTitle {
  margin-left: 10%;
}

.txtTitleDes {
  font-size: 30px;
  color: #141e47;
}

.contentCerrarDes {
  width: 20%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.txtCerrarDes {
  font-size: 20px;
  color: black;
}

.contentDes {
  width: 100%;
  height: 90%;
  display: flex;
  flex-direction: row;
}

.contentDesText {
  width: 60%;
  height: 100%;
}

.divDesText{
    width: 90%;
    height: 90%;
    margin-left: 6%;
}

.txtDes{
    color: #141e47;
    font-size: 18px;
}

.contentSwiperImg {
  width: 40%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.swiperImg {
  width: 92%;
  height: 90%;
}

.contentVisitar {
  width: 100%;
  height: 10%;
  display: flex;
  justify-content: right;
  align-items: center;
}

.divVisitar {
  margin-right: 10%;
}

.txtVisitar {
  color: #141e47;
  font-size: 25px;
}

.sliderImg {
  width: 100%;
  height: 100%;
}

.contentImagenes {
  width: 100%; /* Establece el ancho máximo del contenedor */
  height: 100%; /* Establece la altura máxima del contenedor */
  display: flex;
  justify-content: center;
  align-items: center;
}

.imagenesProyect {
  width: auto; /* Restablece el ancho automático */
  height: auto; /* Restablece la altura automática */
  max-width: 100%; /* Establece el ancho máximo de la imagen */
  max-height: 100%; /* Establece la altura máxima de la imagen */
  object-fit: contain; /* Escala la imagen para que se ajuste completamente al contenedor sin distorsionarla */
}

@media (max-width: 700px) {
  .contentResumeProyect {
    flex-direction: column;
    height: 70%;
  }
  .contentProyects {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
  }

  .contentImgProyect {
    height: 30%;
  }

  .txtResumen {
    font-size: 16px;
  }

  .titleTarjetProyect {
    height: 8%;
  }

  .txtTitleProyect {
    font-size: 22px;
  }

  .contentButonVer {
    height: 10%;
  }

  .contentResum {
    align-items: normal;
  }

  .backImgArrow {
    width: 60%;
    height: 10%;
  }

  .containerTarjetsPro {
    margin-top: 5%;
    justify-content: start;
  }

  .contentTitleProyects {
    height: 10%;
  }

  .contentDes {
    flex-direction: column;
  }

  .contentDesText{
    width: 100%;
    height: 55%;
  }

  .contentSwiperImg{
    width: 100%;
    height: 45%;
  }

  .subContenedorAlert{
    width: 80%;
    height: 90%;
  }

  .divVisitar{
    margin-bottom: 3%;
    margin-top: 3%; }

  .txtDes{
    font-size: 17px;
  }

  .contentTitleDes{
    height: 6%;
  }

  .contentVisitar {
    height: 12%;
  }

  .swiperImg{
    height: 88%;
  }
}
