.container {
  background-color: black; /* Fondo negro */
  max-height: 100vh; /* Altura de la pantalla completa */
  display: flex;
  justify-content: center;
  align-items: center;
}

.image-container {
  position: relative;
  width: 100%; /* Ancho de la imagen */
  height: 95vh;
  max-height: 100vh;
}

.image-container img {
  width: 100%;
  height: auto;
  display: block;
  height: 95vh;
  max-height: 100vh;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 1)
  ); /* Degradado */
}

.overlay1 {
  position: absolute;
  top: 0;
  left: 0;
  width: 30%;
  height: 100%;
  background: linear-gradient(
    to left,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 1)
  ); /* Degradado */
}

.overlay2 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    to right,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.9)
  ); /* Degradado */
}

.overlay3 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    to top,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.9)
  ); /* Degradado */
}

@media (max-width: 700px) {
  .container {
    background-color: black; /* Fondo negro */
    max-height: 85vh; /* Altura de la pantalla completa */
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .image-container {
    position: relative;
    width: 100%; /* Ancho de la imagen */
    height: 85vh;
    max-height: 85vh;
  }

  .image-container img {
    width: 100%;
    height: auto;
    display: block;
    height: 70vh;
    max-height: 100vh;
  }

  
}
