
.page {
    position: relative;
    overflow: hidden;   
    background-color: black;
    height: 100vh;
  }
  
  .hamburguesaContainer {
    position: absolute;
    top: 2%;
    left: 1%;
    z-index: 999; /* Ajusta el valor según sea necesario para que la hamburguesa esté por encima de otros elementos */
  }
  
  .tarjetaContainer{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 999;
    width: 35%;
    height: 100vh;
  }
  
  .content {
    display: flex;
    width: 100%;
    height: 100%;
  }
  
  .subContent{
    width: 40%;
    flex-direction: column;
  }
  
  .contenInf{
    width: 100%;
    height: 50vh;
    display: inline-flex;
    justify-content: center;
    align-items: end;
  }
  
  .contenSlogan{
    width: 100%;
    align-items: center;
    justify-content: center;
    display: flex;
    margin-top: 1%;
  }
  
  .txtSlogan{
    color: white;
    text-align: center;
    font-size: 18px;
    width: 58%;
  }
  
  .contenImg{
    width: 60%;
    display: inline;
    align-items: center;
    justify-content: center;
  }
  
  
  
  @keyframes movimiento-fondo {
    from {
      transform: translateX(0%);
    }
    to {
      transform: translateX(-100%);
    }
  }
  
  /* Enable hover only on non-touch devices */
  @media (hover: hover) and (pointer: fine) {
    .card:hover {
      background: rgba(var(--card-rgb), 0.1);
      border: 1px solid rgba(var(--card-border-rgb), 0.15);
    }
  
    .card:hover span {
      transform: translateX(4px);
    }
  }
  
  @media (prefers-reduced-motion) {
    .card:hover span {
      transform: none;
    }
  }
  
  /* Mobile */
  

    @media (max-width: 700px) {

      .content {
        display: block;
        width: 100%;
        height: 100%;
      }

    .subContent{
      width: 100%;
      height: 30%;
      flex-direction: column;
      align-items: center;
    }

    .contenInf{
      width: 100%;
      height: auto;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      margin-top: 30%;
    }

    .contenSlogan{
      width: 100%;
      align-items: center;
      justify-content: center;
      display: flex;
      margin-top: 1%;
    }

    .txtSlogan{
      color: white;
      text-align: center;
      font-size: 16px;
      width: 88%;
    }

    .contenImg{
      width: 100%;
      height: 70%;
      display: inline;
      align-items: center;
      justify-content: center;
    }

    .tarjetaContainer{
      position: absolute;
      top: 0;
      left: 0;
      z-index: 999;
      width: 100%;
      height: 100vh;
    }
  
    .grid {
      grid-template-columns: 1fr;
      margin-bottom: 120px;
      max-width: 320px;
      text-align: center;
    }
  
    .card {
      padding: 1rem 2.5rem;
    }
  
    .card h2 {
      margin-bottom: 0.5rem;
    }
  
    .center {
      padding: 8rem 0 6rem;
    }
  
    .center::before {
      transform: none;
      height: 300px;
    }
  
    .description {
      font-size: 0.8rem;
    }
  
    .description a {
      padding: 1rem;
    }
  
    .description p,
    .description div {
      display: flex;
      justify-content: center;
      position: fixed;
      width: 100%;
    }
  
    .description p {
      align-items: center;
      inset: 0 0 auto;
      padding: 2rem 1rem 1.4rem;
      border-radius: 0;
      border: none;
      border-bottom: 1px solid rgba(var(--callout-border-rgb), 0.25);
      background: linear-gradient(
        to bottom,
        rgba(var(--background-start-rgb), 1),
        rgba(var(--callout-rgb), 0.5)
      );
      background-clip: padding-box;
      backdrop-filter: blur(24px);
    }
  
    .description div {
      align-items: flex-end;
      pointer-events: none;
      inset: auto 0 0;
      padding: 2rem;
      height: 200px;
      background: linear-gradient(
        to bottom,
        transparent 0%,
        rgb(var(--background-end-rgb)) 40%
      );
      z-index: 1;
    }
  }
  
  /* Tablet and Smaller Desktop */
  @media (min-width: 701px) and (max-width: 1120px) {
    .grid {
      grid-template-columns: repeat(2, 50%);
    }
  }
  
  @media (prefers-color-scheme: dark) {
    .vercelLogo {
      filter: invert(1);
    }
  
    .logo {
      filter: invert(1) drop-shadow(0 0 0.3rem #ffffff70);
    }
  }
  
  @keyframes rotate {
    from {
      transform: rotate(360deg);
    }
    to {
      transform: rotate(0deg);
    }
  }